import React, { Component } from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner'
import PropTypes from 'prop-types'
import InfiniteScroll from "react-infinite-scroll-component"

export class News extends Component {
   
    static defaultProps = {
        country: 'us',
        pageSize: 8,
        category: 'general'
    }

    static propTypes = {
        country: PropTypes.string,
        pageSize: PropTypes.number,
        category: PropTypes.string
    }

    constructor(props) {
        super(props);
        this.state = {
            articles: [],
            loading: true,
            page: 1,
            totalResults: 0
        }}

    async updateNews(){
        this.props.setProgress(10);
        let url = ` https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=000c037552774ebdac2ba72516bb5d9f&page=${this.state.page}&pageSize=${this.props.pageSize} `
        this.setState({loading:true})
        let data = await fetch(url);
        this.props.setProgress(30);
        let parsedData = await data.json()
        this.props.setProgress(70);
        this.setState({
            articles: parsedData.articles,
            totalResults: parsedData.totalResults,
            loading: false 
        })
        this.props.setProgress(100);
    }       

    async componentDidMount(){
        this.updateNews();
    }

    fetchMoreData = async () => {  
        this.setState({page: this.state.page + 1})
        const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=d093053d72bc40248998159804e0e67d&page=${this.state.page}&pageSize=${this.props.pageSize}`;
        let data = await fetch(url);
        let parsedData = await data.json()
        this.setState({
            articles: this.state.articles.concat(parsedData.articles),
            totalResults: parsedData.totalResults
        })
      };

    render() {
        return (
            <>
                <h2 className="text-center my-3">Buzz News - Top {this.props.category} Headlines</h2>
                {this.state.loading&&<Spinner/>}
                <InfiniteScroll
                    dataLength = {this.state.articles.length}
                    next={this.fetchMoreData}
                    hasMore={this.state.articles.length !== this.state.totalResults}
                    loader={<Spinner/>}
                    >
                <div className="container">
                <div className="row ">
                    {this.state.articles.map((element)=>{
                        
                        return <div className="col-md-4" key={element.url}>
                            <NewsItem title={element.title ? element.title.slice(0,70) : "unknown"} newsUrl={element.url}
                            description={element.description?element.description.slice(0,90):"unknown"} author={element.source.name} published={element.publishedAt} imageUrl={element.urlToImage?element.urlToImage:"https://i.ytimg.com/vi/-IcOUB2poZA/hqdefault_live.jpg"}/> 
                            </div>})
                    }          
                </div>
                </div>
                </InfiniteScroll>
            </>
        )
    }
}

export default News
